* {
  box-sizing: border-box;
  font-family: sans-serif;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
    display: block;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

.navbar {
  margin-bottom: 0;
  padding-bottom: 0;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1000;
  flex: 1 0 10%;
  box-shadow: 0 3px 6px rgba(139, 112, 170, 0.404), 0 7px 6px #999;
}

.directions {
  font-weight: 800;
}